/**
 * Manage page resources, localization, etc.
 */
class ResourceManager {
  constructor() {
    let language = window.localStorage.getItem('language');
    if (language == null) {
      window.localStorage.setItem('language', 'en-US');
    }
  }

  static manager;

  /**
   * Gets an instance of this class as singleton
   */
  static getInstance() {
    if (ResourceManager.manager == null ) {
      ResourceManager.manager = new ResourceManager();
    }

    return ResourceManager.manager;
  }

  /**
   * Gets the current language
   */
  getLanguage() {
    return window.localStorage.getItem('language');
  }

  /**
   * Gets a resource without parsing id
   * @param {string} id 
   */
  getRawResource(id) {
    return appData.filter((item) => { return item.id === id })[0];
  }

  /**
   * Gets a resource
   * @param {string} category 
   * @param {string} id 
   */
  getResource(category, id) {
    let identifier = `${this.getLanguage()}.${category}.${id}`;
    return appData.filter((item) => { return item.id === identifier })[0];
  }

  /**
   * Gets all resources that start with the given category and id combination
   * @param {string} category 
   * @param {string} id 
   */
  getResources(category, id) {
    let identifier = `${this.getLanguage()}.${category}.${id}`;
    return appData.filter( (item) => { return item.id.startsWith(identifier) });
  }
}

export default ResourceManager;



